.flex-center, .project-image .image-cover {
  display: flex;
  justify-content: center;
  align-items: center; }

.project-image {
  background-size: contain;
  border-radius: 3px;
  margin: calc(4px + 1vw);
  background-repeat: no-repeat;
  background-position: center;
  overflow: hidden;
  position: relative; }
  .project-image .image-cover {
    cursor: pointer;
    position: absolute;
    height: 100%;
    width: 100%;
    background-color: rgba(88, 85, 85, 0.76);
    color: white;
    border-radius: 3px;
    border: #adadad solid 1px;
    font-size: calc(16px + 1.25vh);
    padding: 10px;
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
    transition: all 250ms; }
  .project-image .image-cover:hover {
    background-color: rgba(223, 217, 217, 0.5);
    color: black;
    font-weight: bold;
    text-shadow: none; }

.project-image::before {
  display: block;
  border: lightgray solid 1px;
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  border-radius: 3px;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box; }

.web {
  height: 255px;
  width: 450px;
  display: flex; }

.mobile {
  height: 450px;
  width: 240px;
  display: flex; }

@media (max-width: 680px), (max-width: 815px) and (orientation: landscape) {
  .project-image .image-cover {
    font-size: calc(14px + 1.25vw); }
  .web {
    width: 250px;
    height: 145px; }
  .mobile {
    height: 255px;
    width: 130px; } }
